/* Footer CSS */
.footer__container{
    background-color: #131313;
    padding: 5rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#footer__logo{
    color: #fff;
    display: flex;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
}
.footer__links{
    width:100%;
    max-width:1000px;
    display: flex;
    justify-content: center;
}
.footer__link--wrapper{
    display: flex;
}
.footer__link--items{
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    margin:16px;
    text-align: left;
    width:160px;
    box-sizing: border-box;
}
.footer__link--items h2{
    margin-bottom: 16px;
    color:#fff;
}
.footer__link--items a{
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
    transition: 0.3 ease-out;
    }
    .footer__link--items a:hover{
        color:#e9e9e9;
        transition: 0.3 ease-out;
}

.social__icon--link{
    color: #fff;
    font-size: 24px;
}
.social__media{
    max-width: 1000px;
    width: 100%;
}
.social__media--wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width:1000px;
    margin: 40px auto 0 auto;
    }
    .social__icons{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width:240px;
    }
    .website__rights{
        color:#fff;
    }

    @media screen and (max-width:820px){
        .footer__links{
            padding-top: 2rem;
     }
     #footer__logo{
        margin-bottom: 2rem;
  }
  .website__rights{
    margin-bottom: 2rem;
  }
  .footer__link--wrapper{
    flex-direction: column;
  }
  .social__media--wrap{
    flex-direction: column;
  }
}
  @media screen  and (max-width:480px){
    .footer__link--items{
        margin: 0;
        padding: 10px;
        width: 100%;
    }
    
  }