.login{
    justify-content: center;
}
.register-left{
    text-align: center;
    color: #fff;
    padding: 30px;
}
.register-left img{
margin-top: 60px;
margin-bottom: 18px;
width: 80px;
animation: mover 1s infinite alternate;
}
.register-left p{
    padding: 20px 20px 0;
}
.register-left .btn-primary{
    border-radius: 1.5em;
    border: none;
    width: 120px;
    font-weight: 600;
    color: #555;
    margin-top: 20px;
    padding: 10px;
}
.register-left .btn-primary:hover{
    background: #000;
}
.register-right{
    border: none;
    background: #f8f8f8;
    border-top-left-radius: 10% 50%;
    border-bottom-left-radius: 10% 50%;
    padding: 50px;
}
.register-right h2{
    text-align: center;
    margin-bottom: 10px;
    color: #555;
}
.register-form{
    padding: 3px;
}
.register-right .btn-primary{
    float: right;

    border-radius: 1.5em;
    border: none;
    width: 120px;
    background-color: #ff9800;
    font-weight: 600;
    color: #555;
    margin-top: 20px;
    padding: 10px;
}
.register-right .btn-primary:hover{
    background-color: #ff5722;
}
@keyframes mover {
    0%{transform: translateY(0);}
    100%{transform: translateY(-20px);}
}